export default [
  {
    path: '/song',
    component: () => import('@/layout/index'),
    children: [
      { path: 'detail', name: 'detail', meta: { title: '音乐列表' }, component: () => import('@/views/song/index') },
      { path: 'now', name: 'now', meta: { title: '音乐列表' }, component: () => import('@/views/song/now-play') },
      { path: 'rank', name: 'rank', meta: { title: '排行榜' }, component: () => import('@/views/song/rank') },
      { path: 'love', name: 'love', meta: { title: '收藏' }, component: () => import('@/views/song/love') },
      { path: 'comment', name: 'comment', meta: { title: '评论' }, component: () => import('@/views/song/comment') }
    ]
  }
]
