import Cookie from 'js-cookie'

const cookie = {
  setCookieSync (name, value, expries) {
    return Cookie.set(name, value, expries)
  },
  getCookieSync (name) {
    return Cookie.get(name)
  },
  removeCookieSync (name) {
    return Cookie.remove(name)
  },
  clearCookieSync (name) {
    return Cookie.clear(name)
  }
}

export default cookie
