import cookies from '@/utils/cookies'

export default {
  namespaced: true,
  state: {
    nickName: cookies.getCookieSync('nickName'),
    userToken: cookies.getCookieSync('userToken')
  },
  getters: {
    userToken: state => state.userToken
  },
  mutations: {
    setUserToken (state, userToken) {
      state.userToken = userToken
    },
    setNickName (state, nickName) {
      state.nickName = nickName
    }
  },
  actions: {
    handleSetToken ({ commit }, params) {
      cookies.setCookieSync('userToken', params)
      commit('setUserToken', params)
      if (params === '') {
        commit('setNickName', '')
      }
    },
    handleSetNickName ({ commit }, params) {
      cookies.setCookieSync('nickName', params)
      commit('setNickName', params)
    }
  }
}
