import cookies from '@/utils/cookies'

export default {
  namespaced: true,
  state: {
    songArr: localStorage.getItem('songArr'),
    songIdx: cookies.getCookieSync('songIdx'),
    song: cookies.getCookieSync('song'),
    songId: cookies.getCookieSync('songId'),
    songLyrics: cookies.getCookieSync('songLyrics'),
    songPicture: cookies.getCookieSync('songPicture'),
    pagePath: cookies.getCookieSync('pagePath'),
    playStatus: false
  },
  getters: {
    song: state => state.song
  },
  mutations: {
    setSongArr (state, songArr) {
      state.songArr = songArr
    },
    setSongIdx (state, songIdx) {
      state.songIdx = songIdx
    },
    setSongId (state, songId) {
      state.songId = songId
    },
    setSong (state, song) {
      state.song = song
    },
    setSongLyrics (state, songLyrics) {
      state.songLyrics = songLyrics
    },
    setSongPicture (state, songPicture) {
      state.songPicture = songPicture
    },
    setPlayStatus (state, status) {
      state.playStatus = status
    },
    setPagePath (state, pagePath) {
      state.pagePath = pagePath
    }
  },
  actions: {
    handleSetPath ({ commit }, params) {
      cookies.setCookieSync('pagePath', params)
      commit('setPagePath', params)
    },
    // 存储总歌曲信息
    handleSetSongArr ({ commit }, params) {
      localStorage.setItem('songArr', JSON.stringify(params))
      commit('setSongArr', JSON.stringify(params))
    },
    // 存储编号
    handleSetSongIdx ({ commit, state }, params) {
      const len = JSON.parse(state.songArr).length - 1
      const idx = parseInt(params)
      let fina = idx < 0 ? 0 : idx > len ? len : parseInt(params)
      cookies.setCookieSync('songIdx', fina)
      commit('setSongIdx', fina)
      commit('setSongId', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_id)
      commit('setSong', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_origin)
      commit('setSongLyrics', JSON.parse(state.songArr)[parseInt(state.songIdx)].lyrics)
      commit('setSongPicture', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_picture)
    },
    // 控制音乐播放状态
    handleSetPlayStatus ({ commit, state }, params) {
      if (typeof state.songIdx === 'undefined') {
        cookies.setCookieSync('songIdx', 0)
        commit('setSongIdx', 0)
      }
      commit('setSongId', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_id)
      commit('setSong', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_origin)
      commit('setSongLyrics', JSON.parse(state.songArr)[parseInt(state.songIdx)].lyrics)
      commit('setSongPicture', JSON.parse(state.songArr)[parseInt(state.songIdx)].song_picture)
      commit('setPlayStatus', params)
    }
  }
}
