import qs from 'qs'
import axios from 'axios'
import cookies from './cookies'

const http = {
  /** get 请求
   * @param {接口地址} url
   * @param {请求参数} params
   */
  get (url, params) {
    return new Promise((resolve, reject) => {
      axios.get(url, { params: params })
        .then((response) => {
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },
  /** post 请求
   * @param {接口地址} url
   * @param {请求参数} params
   */
  post (url, params) {
    return new Promise((resolve, reject) => {
      if (cookies.getCookieSync('userToken')) {
        params.token = cookies.getCookieSync('userToken')
      }
      axios.post(url, qs.stringify(params))
        .then((response) => {
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  }
}

export default http
